/* modern background */
.background-image {
  background-image: url("/public/media/sunsetbg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-width: 100vh;
  overflow-x: hidden;
  /*remove white space at the bottom of the background */

}

