/* Contact info */
.contact-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  padding-top: 5%;
}



/* email styling */

.email-wrapper {
  padding-top: 15px;
}

.email-container {
  background-image: linear-gradient(-45deg, rgb(198, 198, 198), rgb(218, 198, 255));
  font-size: x-large;
  font-family: Arial, Helvetica, sans-serif;
  color: black;
  width: 500px;
  height: 70px;
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-radius: 80px;
  padding-top: 10px;
}

.email-container svg {
  margin-right: 15px;
}

.email-container:hover {
  cursor: pointer;
  background-image: linear-gradient(-45deg, rgb(255, 254, 254), rgb(218, 198, 255));
}



.popup-container {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(3, 212, 13, 0.8);
  color: rgb(255, 255, 255);
  padding: 10px 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  animation: fade-in 0.5s ease-in-out;
}

.popup-content {
  display: flex;
  align-items: center;
  gap: 10px;
}

.popup-content span {
  font-size: 18px;
}

.popup-content svg {
  font-size: 24px;
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: translate(-50%, 20px);


  }

  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

